<template>
  <div>
    <div class="search">
      <div class="searchGood">
        <form action="/">
          <van-search
            v-model="value"
            shape="round"
            show-action
            placeholder="请输入搜索关键词"
            @cancel="onCancel"
          >
            <template #action>
              <div @click="onSearch">搜索</div>
            </template>
          </van-search>
        </form>
        <div class="box">
          <div class="box1">热门搜索</div>
          <div class="box2">
            <div
              v-for="(item, index) in list"
              :key="index"
              @click="showimg = true"
            >
              {{ item }}
            </div>
          </div>
        </div>
       <quality :item="reslist"></quality>
       <recommendent :showimg="showimg" :hotlist="hotlist"></recommendent>
      </div>
    </div>
  </div>
</template>
<script>
import Recommendent from '@/components/Recommendent'
import Quality from "@/components/Quality";
export default {
  data() {
    return {
      value: "",
      list: [],
      hotlist: [],
      showimg: false,
      reslist: [],
    };
  },
  components:{
      Quality,
      Recommendent
  },
  created() {
    this.$axios.get("/api/search/keyword").then((d) => {
      this.list = d.data.data;
    });
    this.$axios.get("/api/product/hot?page=1&limit=20").then((d) => {
      this.hotlist = d.data.data;
      console.log(this.hotlist);
    });
  },
  methods: {
    onSearch() {
      console.log(this.value);
      this.$axios
        .get("/api/products?page=1&limit=20&keyword=" + this.value)
        .then((d) => {
          this.reslist = d.data.data;
        });
    },
    onCancel() {},
  },
};
</script>
<style lang="less" scoped>
.search {
  .searchGood {
    .box {
      .box1 {
        font-size: 14px;
        color: #999;
        margin: 25px 15px 15px 12px;
      }
      .box2 {
        display: flex;
        font-size: 14px;
        color: #333;
        div {
          line-height: 30px;
          margin: 0 0 10px 10px;
          padding: 0 10.5px;
          border: 1px solid #aaa;
        }
      }
    }
  }
 
  
}
</style>